import { Box, Link, Typography } from "@mui/material"
import { BIG_SUMMARY_SOURCE_HEIGHT } from "@rp/common/settings"
import { LlmExternalSource } from "@rp/react/tanstack-api/llm-summary"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { ExternalFavicon } from "./ExternalFavicon"

type Props = {
  source: LlmExternalSource
  onClick?: () => void
}

const ExternalSourceDisplay = ({ source, onClick }: Props) => {
  const { hd, url, faviconUrl, name } = source
  const height = BIG_SUMMARY_SOURCE_HEIGHT - 2

  return (
    <Link
      onClick={onClick}
      href={url}
      target="_blank"
      rel="noopener noreferrer"
      sx={theme => ({
        zIndex: 0,
        maxWidth: 276,
        height,
        border: `1px solid ${theme.palette.outline.border}`,
        borderRadius: 2,
        display: "flex",
        color: "inherit",
        "&:hover": {
          cursor: "pointer",
          backgroundColor: "elevation.dp08",
          textDecoration: "none",
        },
        "&:visited": {
          color: "inherit",
        },
      })}
    >
      <Box sx={{ flexGrow: 0, p: "12px", overflow: "hidden", width: 276 }}>
        <EllipsisTooltip
          vertical
          tooltipText={hd}
          typographyProps={{ style: { lineHeight: 1.2, minHeight: 40, color: "text.primary" } }}
        >
          {hd}
        </EllipsisTooltip>

        <Box
          sx={{
            pt: 1,
            "& span": { color: "text.secondary" },
            display: "flex",
            alignItems: "center",
            gap: 0.5,
          }}
        >
          <ExternalFavicon
            faviconUrl={faviconUrl}
            alt={name}
            sx={{ display: "flex", "& svg, & img": { height: 16, width: 16 } }}
          />
          <Typography
            component="span"
            sx={{
              fontSize: 12,
              overflow: "hidden",
              whiteSpace: "nowrap",
              textOverflow: "ellipsis",
            }}
          >
            {name}
          </Typography>
        </Box>
      </Box>
    </Link>
  )
}
export { ExternalSourceDisplay }
